import React from 'react';
import { Link } from "react-router-dom";

const NoData = () => {
    return (
        <div className="no-data">
            <div className="no-data--text">No Data Found</div>
            <div className="no-data--home">
                <Link to="/">Home</Link>
            </div>
        </div>
    );
};

export default NoData;