import CartActionTypes from './CartActionTypes';

const INITIAL_STATE = {
    cart: []
};

const cartReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case CartActionTypes.ADD_TO_CART: {
            return {
                ...state,
                cart: addItemToCart(state.cart, action.payload.productID)
            };
        }

        case CartActionTypes.REMOVE_FROM_CART: {
            return {
                ...state,
                cart: removeItemFromCart(state.cart, action.payload.productID)
            }
        }

        case CartActionTypes.REMOVE_PRODUCT_FROM_CART: {
            return {
                ...state,
                cart: removeProductFromCart(state.cart, action.payload.productID)
            }
        }

        default: return state;
    }
}

export const addItemToCart = (cartItems, productID) => {
    const existingCartProduct = cartItems.find(product => product.productID === productID);
    if (existingCartProduct) {
        return cartItems.map(product =>
            product.productID === productID ?
                { productID, quantity: product.quantity + 1 }
                : product
        )
    }
    return [...cartItems, { productID, quantity: 1 }];
};

export const removeItemFromCart = (cartItems, productID) => {
    const existingCartProduct = cartItems.find(
        product => product.productID === productID
    );
    if (existingCartProduct.quantity === 1) {
        return cartItems.filter(product => product.productID !== productID);
    }
    return cartItems.map(product =>
        product.productID === productID ?
            { productID, quantity: product.quantity - 1 }
            : product
    );
};

export const removeProductFromCart = (cartItems, productID) => {
    const existingCartProduct = cartItems.find(
        product => product.productID === productID
    );
    if (existingCartProduct) {
        return cartItems.filter(product => product.productID !== productID)
        // return cartItems.reduce((acc, curr) => {
        //     console.log("acc", acc);
        //     console.log("curr", curr);
        //     if (curr.productID === existingCartProduct.productID) acc.push(curr);
        //     return acc;
        // }, []);
        // console.log('removeProductFromCart::cartItems', cartItems);
        // console.log('removeProductFromCart::existingCartProduct', existingCartProduct);
    }
    return cartItems;
};

export default cartReducer;